import React from 'react';
import './Card.css';
import { BiStar } from 'react-icons/bi';


class Card extends React.Component{

	render() {

		const {id, name, username, email, address, phone, website, company } = this.props;
		let detAdd = `${address.suite}, ${address.street}, ${address.city}`;
		let editedPhone = phone.slice(0,phone.length-6);
		let editedWebsite = `https://www.${website}.com`
		return(

			<div className='card-content tc'>
				<span className='staricon'><BiStar className='star'/> </span>
				<img className='robo-image' alt='robots' src={`https://robohash.org/${id}?200x200`}/>
				<span className='default-info'>
					<h2> {name} </h2>
				</span>
				<div className='information'>
					<p> <box-icon name='user'></box-icon> <span className='toolkit'> Name </span> <span className='values'>{name}</span> </p>
					<p> <box-icon name='user-circle'></box-icon> <span className='toolkit'> Username </span> <span className='values'>{username}</span> </p>
					<p> <box-icon name='paper-plane'></box-icon><span className='toolkit'> Email </span> <span className='values'>{email}</span> </p>
					<p> <box-icon name='home-alt'></box-icon><span className='toolkit'> Address </span> <span className='values'>{detAdd}</span></p>
					<p> <box-icon name='phone'></box-icon><span className='toolkit'> Phone Number </span><span className='values'>{editedPhone}</span> </p>
					<p> <box-icon name='world'></box-icon><span className='toolkit'> Website </span> <span className='values'>{editedWebsite}</span> </p>
					<p> <box-icon name='building-house'></box-icon><span className='toolkit'> Company </span><span className='values'> {company.name}</span> </p>
					
				</div>
			</div>
			)
	}
}

export default Card